
html {
	height: 100%;
	margin: 0;
	padding: 0;
}
body {
	position: relative;
	width: 100%;
	height: 100%;
	min-height: 768px;
	min-width: 1280px;
	/*overflow: hidden;*/
}
hr {
	margin: 0;
}
#app {
	height: 100%;
	overflow: hidden;
	width: 100%;
}
.notify{
	position: absolute;
	width: 600px;
	height: 100px;
	top: -45px;
	left: calc(50% - 300px);
	background-image: url(../../static/img/notice.587051f.png);
	background-size: 100% 100%;
	-webkit-transition-duration: 0.5s;
	        transition-duration: 0.5s;
	z-index: 10;
}
.notify:hover{
	-webkit-transform: translateY(30px);
	        transform: translateY(30px);
}
.notify-content{
	color: white;
	position: relative;
	overflow: hidden;
	height: 30px;
	width: 400px;
	margin: 60px auto 0;
}
.notify-content span{
	position: absolute;
	z-index: 99;
	font-size: 16px;
	display: inline-block;
	height: 18px;
	overflow: hidden;
}
section {
	padding-right: 250px;
	height: calc(100% - 60px);
	width: 100%;
	padding-top: 10px;
	padding-left: 10px;
}
header {
	padding: 10px;
	position: relative;
	border-bottom: 1px solid #dddddd;
	height: 55px;
	-webkit-box-shadow: 0px 1px 10px #dddddd;
	        box-shadow: 0px 1px 10px #dddddd;
	background-color: rgba(0, 0, 0, 0.3);
}
#app footer {
	position: absolute;
	max-width: 100%;
	width: 100%;
	bottom: 0px;
	color: #222222;
	text-align: center;
	line-height: 40px;
	background-color: rgba(255, 255, 255, 0.7);
	font-size: 12px;
}
label.error {
	color: #a94442;
}
.avatar-img {
	width: 100%;
}
.user_avatar {
	height: 150px;
	width: 150px;
	background-color: grey;
	margin: 0 auto 20px;
	border-radius: 50%;
	overflow: hidden;
	pointer-events: none;
	position: relative;
}
.user_avatar_cover {
	position: absolute;
	background-color: rgba(0, 0, 0, 0.3);
	font-size: 16px;
	height: 60px;
	width: 100%;
	bottom: -10px;
	line-height: 30px;
	display: none;
	pointer-events: none;
}
#btn-login {
	height: 40px;
	width: 170px;
	background: none;
	outline: none;
	border: none;
	color: #DFDFDF;
	font-size: 16px;
	-webkit-transition-duration: 0.3s;
	        transition-duration: 0.3s;
	background-image: url(../../static/img/diablo-btn.62cd340.png);
	background-size: 100% 100%;
}
#btn-login:hover {
	-webkit-transform: scale(1.1, 1.1);
	        transform: scale(1.1, 1.1);
	color: gold;
}
.user_info_panel button {
	height: 40px;
	width: 170px;
	border: none;
	outline: none;
	background: none;
	background-image: url(../../static/img/diablo-btn.62cd340.png);
	background-size: 100% 100%;
	color: #DFDFDF;
	font-weight: bold;
	font-size: 14px;
	-webkit-transition-duration: 0.3s;
	        transition-duration: 0.3s;
}
.user_info_panel button:hover {
	color: gold;
}
.window_close {
	position: absolute;
	height: 15px;
	width: 15px;
	top: 15px;
	right: 15px;
	background-color: rgba(255, 255, 255, 1);
	cursor: pointer;
}
