.rank-table {
	-webkit-transition-duration: 0.5s;
	        transition-duration: 0.5s;
	margin: 50px auto 0 auto;
	width: 95% !important;
	background-color: rgba(0, 0, 0, 0.5);
	color: #EEEEEE;
	border-radius: 10px;
	font-family: "\65B9\6B63\5170\4EAD\8D85\7EC6\9ED1\7B80\4F53";
}
.rank-table thead {
	font-size: 18px;
}
.rank-table tbody tr {
	height: 80px;
	font-size: 38px;
	padding: 0;
	line-height: 80px;
	font-weight: bold;
	-webkit-transition-duration: 0.3s;
	        transition-duration: 0.3s;
	text-align: center;
}
.rank-table tbody tr:hover {
	color: #555555;
	font-weight: bold;
	-webkit-transform: scale(1.05, 1.2);
	        transform: scale(1.05, 1.2);
}
.rank-table tbody tr:nth-of-type(n+4) {
	height: 40px;
	font-size: 16px;
	line-height: 40px;
}
.rank-table tbody tr:nth-of-type(1) {
	font-size: 40px;
}
.rank-table tbody tr:nth-of-type(2) {
	font-size: 35px;
}
.rank-table tbody tr:nth-of-type(3) {
	font-size: 30px;
}
.table-avatar-cover {
	width: 60px;
	height: 60px;
	overflow: hidden;
	border-radius: 50%;
}
.question_collec {
	height: calc(100% - 490px);
