#chat .chat-avatar-cover{
	width: 40px;
	height: 40px;
	overflow: hidden;
	border-radius: 50%;
	margin-right: 10px;
	display: inline-block;
}
#chat .user-block{
	position: relative;
	zoom: 1;
	padding: 5px;
	-webkit-transition-duration: 0.2s;
	        transition-duration: 0.2s;
	margin: 10px;
}
#chat .user-block>div{
	float: left;
	font-size: 24px;
	line-height: 40px;
	color: white;
}
#chat .user-block:after{
	content: '.';
	height: 0;
	display: block;
	clear: both;
	visibility: hidden;
}
#chat .last-speak{
	display: block;
	padding-top: 5px;
	color: #aaaaaa !important;
	font-size: 10px !important;
	line-height: 10px !important;
	clear: both;
}
#chat .user-block:hover{
	-webkit-transform: scale(1.1,1.1);
	        transform: scale(1.1,1.1);
}
#chat .online-state-online{
